<template>
  <div class="index-bg">
    <div v-if="local === 'zh-tw'">
      <indexCn :openUsdt="openUsdt" :allcombo="allcombo" @getAllCombo="getAllCombo"></indexCn>
    </div>
    <div v-else>
      <indexEn :openUsdt="openUsdt" :allcombo="allcombo" @getAllCombo="getAllCombo"></indexEn>
    </div>
  </div>
</template>
<script>
import { reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import { getLang, setLang } from "@/utils/i18n"
import {useStore} from "vuex";
import indexEn from "./indexEn.vue";
import indexCn from "./indexCn.vue";
import config from "@/utils/config";
import tokenAbi from "@/utils/abi";
import openAbi from "@/utils/openRole";
export default {
  components: {
    indexEn,
    indexCn
  },
  setup() {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      local: getLang().toLowerCase() || "en",
      OpenContract: null,
      UsdtContract: null,
      openUsdt: '',
      decimals: '',
      allcombo: [],
      initContract: () => {
        data.OpenContract = new data.web3.eth.Contract(openAbi, config.OpenAddress)
        data.UsdtContract = new data.web3.eth.Contract(tokenAbi, config.UsdtAddress)
        data.initData()
      },
      initData: () => {
        data.getopenUsdt()
        data.getAllCombo()
      },
      getopenUsdt: async() => {
        data.openUsdt = await data.OpenContract.methods.openBindUsdt().call();
        data.decimals = await data.UsdtContract.methods.decimals().call();
      },
      getAllCombo: async() => {
        data.allcombo = await data.OpenContract.methods.getAllCombo().call();
      },
    })
    if (data.address) {
      data.initContract()
    }
     watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
</style>
