import { getLang, setLang } from "@/utils/i18n"
import { Multicall } from 'ethereum-multicall';
import { MaxUint256, AddressZero } from "@ethersproject/constants";
export default {
  components: {
    Footer
  },
  props: {
    allcombo: {
      type: Array,
