    },
  },
  emits: ['getAllCombo'],
  setup(props, {emit}) {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      Bignumber: Bignumber,
      allcombo: computed({
        get () {
          return props.allcombo
        }
      }),
      openUsdt: computed({
        get () {
          return props.openUsdt
        }
      }),
      $t: t,
      local: getLang().toLowerCase() || "en",
      intro: intro.get() || config.RootAddress,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      showMask: false,
      showPopover: false,
      showUserInfo: false,
      UsdtBlance: '',
      decimals: '',
      shareUrl: '',
      allowance: '',
      NewAllowance: '',
      allFarm: [],
      NewNUmber: [],
      usrPotato: 0,
      Newcombo: [],
      info: '<p class="MsoNormal" style="text-align:left;text-indent:90pt;"><b><span style="font-size:9px;"></span><span style="font-size:9px;">遊戲預售告知說明</span><span></span></b> </p><p class="MsoNormal" style="text-indent:48.0pt;"><b></b> </p><p class="MsoNormal" style="text-align:left;text-indent:96pt;"><b><span style="font-size:9px;"></span><span style="font-size:9px;">稀缺幸運寶盒</span><span></span></b> </p><p class="MsoListParagraph" style="margin-left:36.0pt;text-indent:-36.0pt;"><span style="font-size:9px;">一、&nbsp;&nbsp;&nbsp;&nbsp;包含的資產及技能： </span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;">幸運寶盒最多會包含八種</span><span style="font-size:9px;">NFT</span><span style="font-size:9px;">資產，分別為：</span><span> </span> </p><p class="MsoNormal"><span style="font-size:9px;">1、遊戲攻防角色：狐貍</span><a name="undefined"></a><span style="font-size:9px;">，絶技：幫主人偷取土豆，掠獲財富；狗，絶技：替主人抓捕狐貍，保衛土豆；熊，絶技：幫主人襲擊狗，贏得賞金；槍，絶技：替主人捕獵熊，守護莊園！</span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;"><span style="font-size:9px;">稀有封神角色：九尾狐、哮天犬、太極熊、黃金槍，神技：不死真身，傲視群雄；豪取搶奪，</span><span style="font-size:9px;"></span><span style="font-size:9px;">無法無天！</span></span> </p><p class="MsoNormal"><span style="font-size:9px;">2、稀有封神角色：封神稀有品種是遊戲</span><span style="font-size:9px;">BUG</span><span style="font-size:9px;">級的驚喜設計，除對應相剋的封神角色可以壓制外，劫掠縱橫，聚斂財富，所心所欲。即使神奇的正好被相剋的封神角色捕獲，也不參與銷毀機制，但所有權與支配權屬於新主人，支援鏈上</span><span style="font-size:9px;">NFT</span><span style="font-size:9px;">交易所轉售。</span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;">二、資產種類及技術屬性</span><span></span> </p><p class="MsoNormal" style="text-indent:28.0pt;"><span style="font-size:9px;">幸運寶盒每個統一價格為</span><span style="font-size:9px;">299</span><span style="font-size:9px;">美金。內資產將涵蓋（狐貍、狗、熊、槍）四種遊戲角色和（九尾狐、哮天犬、太極熊、黃金槍）四種封神稀有品種，總計八種遊戲角色資產。玩家獲得的每一款角色都具有唯一編碼，都是基於</span><span style="font-size:9px;">BSC</span><span style="font-size:9px;">公鏈</span><a name="undefined"></a><span style="font-size:9px;">BEP</span><span style="font-size:9px;">1155</span><span style="font-size:9px;">協議與</span><span style="font-size:9px;">BEP721</span><span style="font-size:9px;">協議發行的</span><span style="font-size:9px;">NFT</span><span style="font-size:9px;">數字藏品，總量根據土地的發售數量按固定比例對標恆量發售，除封神稀有品種外，其他角色捕獲既銷毀，通縮機制，除支援遊戲內</span><span style="font-size:9px;">NFT</span><span style="font-size:9px;">交易所交易外，也支援任何鏈上</span><span style="font-size:9px;">NFT</span><span style="font-size:9px;">交易所。</span> </p><p class="MsoNormal"><span style="font-size:9px;">三、預售機制與分配規則</span><span></span> </p><p class="MsoNormal" style="text-indent:28.0pt;"><span style="font-size:9px;">幸運寶盒每個的預售價格都一致，採用盲盒機制，限量發售；購買不限，售完為止。每個寶盒保底獲取</span><span style="font-size:9px;">16</span><span style="font-size:9px;">個</span><span style="font-size:9px;">NFT</span><span style="font-size:9px;">角色代幣，所有預售期的遊戲角色封盒價格比遊戲上綫后價格均超值優惠。購買后具體每個玩家競購到的寶盒含有的角色品種及價值都隨機分配，全憑運氣。總量限定的封神稀有角色為：</span><span style="font-size:9px;">9</span><span style="font-size:9px;">只九尾狐，</span><span style="font-size:9px;">27</span><span style="font-size:9px;">條哮天犬，</span><span style="font-size:9px;">3</span><span style="font-size:9px;">頭太極熊，</span><span style="font-size:9px;">9</span><span style="font-size:9px;">把黃金槍，只在預售期的幸運寶盒中隨機開出，公開合約地址接受監督，遊戲正式上綫后平臺不再發售封神稀有角色的</span><span style="font-size:9px;">NFT</span><span style="font-size:9px;">遊戲資產。 </span><span></span> </p><p class="MsoNormal"><br /></p><p class="MsoNormal" style="text-align:center;"><b><span style="font-size:9px;">限量超值寶箱</span><span></span></b> </p><p class="MsoNormal"><span style="font-size:9px;">一、 土地資產屬性及產值 介紹</span><span> </span> </p><p class="MsoNormal"><span style="font-size:9px;">1、黃土地</span><a name="undefined"></a><span style="font-size:9px;">，土地價值</span><span style="font-size:9px;">:</span><span style="font-size:9px;">獲取遊戲資格，擁有土豆農場，享有支配遊戲角色的權益，每日土豆（</span><span style="font-size:9px;">PTC</span><span style="font-size:9px;">）產量為購買該土地的土豆幣數量的</span><span style="font-size:9px;">1%</span><span style="font-size:9px;">之多。</span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;">2、紅土地，土地價值</span><span style="font-size:9px;">:</span><span style="font-size:9px;">享受</span><a name="undefined"></a><span style="font-size:9px;">黃土地所有權益及土豆農場一切權益，每日土豆（</span><span style="font-size:9px;">PTC</span><span style="font-size:9px;">）產量為黃土地的</span><span style="font-size:9px;">3</span><span style="font-size:9px;">倍。</span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;">3、黑土地，土地價值</span><span style="font-size:9px;">:</span><span style="font-size:9px;">享受紅土地所有權益及土豆莊園遊戲一切權益，每日土豆（</span><span style="font-size:9px;">PTC</span><span style="font-size:9px;">）產量為紅土地的</span><span style="font-size:9px;">3</span><span style="font-size:9px;">倍。</span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;">二、超值寶箱價格及配置介紹：</span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;">寶箱按土地屬性可分為三種分別為：</span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;">1、寶箱</span><span style="font-size:9px;">A</span><span style="font-size:9px;">（</span><span style="font-size:9px;">591</span><span style="font-size:9px;">美元）：黃土地</span><span style="font-size:9px;">9</span><span style="font-size:9px;">塊，狗</span><span style="font-size:9px;">9</span><span style="font-size:9px;">條，槍</span><span style="font-size:9px;">1</span><span style="font-size:9px;">把，土豆幣</span><span style="font-size:9px;">200</span><span style="font-size:9px;">枚。</span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;">2、寶箱</span><span style="font-size:9px;">B</span><span style="font-size:9px;">（</span><span style="font-size:9px;">1137</span><span style="font-size:9px;">美元）：紅土地</span><span style="font-size:9px;">9</span><span style="font-size:9px;">塊，狗</span><span style="font-size:9px;">9</span><span style="font-size:9px;">條，槍</span><span style="font-size:9px;">1</span><span style="font-size:9px;">把，土豆幣</span><span style="font-size:9px;">400</span><span style="font-size:9px;">枚。</span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;">3、寶箱</span><span style="font-size:9px;">C</span><span style="font-size:9px;">（</span><span style="font-size:9px;">2769</span><span style="font-size:9px;">美元）：黑土地</span><span style="font-size:9px;">9</span><span style="font-size:9px;">塊，狗</span><span style="font-size:9px;">9</span><span style="font-size:9px;">條，槍</span><span style="font-size:9px;">1</span><span style="font-size:9px;">把，土豆幣</span><span style="font-size:9px;">800</span><span style="font-size:9px;">枚。</span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;">三、土地種類及技術屬性</span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;">限量超值寶箱採用明確告知其箱內遊戲資產數量與種類的透明搶購機制。共分為</span><span style="font-size:9px;">A</span><span style="font-size:9px;">、</span><span style="font-size:9px;">B</span><span style="font-size:9px;">、</span><span style="font-size:9px;">C</span><span style="font-size:9px;">三種寶箱，每一種寶箱包含的土地屬性與優惠配置的土豆幣（</span><span style="font-size:9px;">PTC</span><span style="font-size:9px;">）數量不同，導致其三種寶箱的價值和價格也不同。其中每一塊黃土地、紅土地、黑土地都</span><a name="undefined"></a><span style="font-size:9px;">是基於</span><span style="font-size:9px;">BSC</span><span style="font-size:9px;">公鏈</span><span style="font-size:9px;">BEP1155</span><span style="font-size:9px;">協議，根據土豆莊園遊戲的需求，恆量開發的</span><span style="font-size:9px;">NFT</span><span style="font-size:9px;">數字資產，具有唯一性和稀缺性，並支援鏈上交易。前期土地的主要功能是，通過玩家種植而產出土豆（</span><span style="font-size:9px;">PTC</span><span style="font-size:9px;">）的挖礦功能。挖礦期結束后，遊戲將對持有者開放土地元宇宙沙盒功能，土地領主可自主建設該土地和支配其用途，並可以根據地塊位置與設施建設情況，在所有鏈上</span><span style="font-size:9px;">NFT</span><span style="font-size:9px;">交易所定價售賣。</span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;">四、土豆幣用途及技術屬性</span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;">限量超值寶箱除含有土地外，還優惠配置了防守型的遊戲角色狗與槍，從而保護購買土地的新手玩家獲得的土地收益，不遭受其他老玩家的攻擊掠奪。寶箱內還配置了少量可在遊戲店鋪內購買上架商品的通用貨幣土豆幣。土豆幣是玩家在遊戲內一切生態場景，進行消費娛樂時的唯一數字資產。土豆幣（</span><span style="font-size:9px;">PTC</span><span style="font-size:9px;">）是基於</span><span style="font-size:9px;">BSC</span><span style="font-size:9px;">公鏈</span><span style="font-size:9px;">BEP20</span><span style="font-size:9px;">協議，恆量開發的遊戲通證。所有參與遊戲的玩家用來購置土地的土豆幣，都將被鏈上合約公開銷毀，行成日銷毀量甚至會高於日產出量的極致通縮機制，從而實現一幣難求，幣價持續攀升的經濟模型。土豆幣支援</span><span style="font-size:9px;">DEX</span><span style="font-size:9px;">及主流交易所交易，是土豆莊園遊戲資產與玩家現實財富兌換交易的主要媒介。預售期為防止玩家遊戲上綫前在</span><span style="font-size:9px;">DEX</span><span style="font-size:9px;">私建交易對，</span><span style="font-size:9px;">PTC</span><span style="font-size:9px;">通證將延遲到遊戲上綫後，發到參與競購的玩家錢包鏈上地址中。</span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;">五、遊戲公測進度時間綫</span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;">2021年</span><span style="font-size:9px;">12</span><span style="font-size:9px;">月上旬 &nbsp;全球幸運寶盒與超值套餐預售</span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;">2021年</span><span style="font-size:9px;">12</span><span style="font-size:9px;">月上旬 </span><b> </b><span style="font-size:9px;">預售所有道具裝備開箱</span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;">2021年</span><span style="font-size:9px;">12</span><span style="font-size:9px;">月中旬 &nbsp;遊戲內測版發佈</span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;">2021年</span><span style="font-size:9px;">12</span><span style="font-size:9px;">月中旬 &nbsp;美國邁阿密社區見面會</span><b><span></span></b> </p><p class="MsoNormal"><span style="font-size:9px;">2021年</span><span style="font-size:9px;">12</span><span style="font-size:9px;">月下旬 &nbsp;歐洲柏林技術研討會</span><b><span></span></b> </p><p class="MsoNormal"><span style="font-size:9px;">2022年</span><span style="font-size:9px;">1</span><span style="font-size:9px;">月 上旬 &nbsp;日本東京全球上線發佈會</span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;">2022年</span><span style="font-size:9px;">1</span><span style="font-size:9px;">月 上旬 &nbsp;全球公測版上線</span><span></span> </p><p class="MsoNormal"><br /></p><p class="MsoNormal" style="text-align:center;"><span style="font-size:9px;">本告知說明最終解釋權歸</span><span style="font-size:9px;">PotatomanorFund</span><span style="font-size:9px;">所有。</span> </p><p class="MsoNormal"><br /></p>',
      actions : [
        { text: '中文' },
        { text: 'English' }
      ],
      onSelect: (val) => {
        let lang = val.text === 'English' ? 'en' : 'zh-tw'
        setLang(lang)
        data.local = lang
        location.reload()
      },
      initContract: () => {
        data.OpenContract = new data.web3.eth.Contract(openAbi, config.OpenAddress)
        data.UsdtContract = new data.web3.eth.Contract(tokenAbi, config.UsdtAddress)
        data.initData()
      },
      initData: () => {
        data.shareUrl = `${window.location.origin}${window.location.pathname}#/?intro=${data.address}`
        data.getopenUsdt()
        data.getTokenAllowance()
        data.getAllFarmType()
      },
      getTokenAllowance: async () => {
        let wResult = await data.UsdtContract.methods.allowance(data.address, config.OpenAddress).call();
        data.NewAllowance = wResult;
      },
      approve: async () => {
        if (!data.address) {
          Toast(t('game.連線錢包'))
          return
        }
        data.allowance = await data.UsdtContract.methods.approve(config.OpenAddress, MaxUint256).send({from: data.address}).then(() => {
          Toast(t('game.授權成功'))
        })
        data.getTokenAllowance()
      },
      getopenUsdt: async() => {
        data.UsdtBlance = await data.UsdtContract.methods.balanceOf(data.address).call()
        data.decimals = await data.UsdtContract.methods.decimals().call();
      },
      buyCombo: async(price, id) => {
        if (!data.address) {
          Toast(t('game.連線錢包'))
          return
        }
        if(Number(price) > Number(data.UsdtBlance)) {
          Toast('USDT餘額不足')
          return
        }
        let gas = await data.OpenContract.methods.buyCombo(id, data.intro).estimateGas({from: data.address})
        await data.OpenContract.methods.buyCombo(id, data.intro).send({from: data.address, gas: gas}).then((res) => {
          Toast('購買成功')
          emit('getAllCombo')
        })
      },
      getComboNumber: async() => {
        let allcomboType = []
        for(var i = 0; i < data.allcombo.length; i++) {
          allcomboType.push({ reference: 'allcomboType', methodName: 'userComboAmount', methodParameters: [data.address, data.allcombo[i].id]});
        }
        const comboCallContext = [
          {
            reference: 'OpenContract',
            contractAddress: config.OpenAddress,
            abi: openAbi,
            calls: allcomboType
          }
        ]
        const multicallCom = new Multicall({ multicallCustomContractAddress: config.multicallAddress, web3Instance: data.web3, tryAggregate: false });
        const resultsCombo = await multicallCom.call(comboCallContext);
        let comboArr = resultsCombo.results.OpenContract.callsReturnContext
        data.Newcombo = []
        for(var a = 0; a < comboArr.length; a++) {
          data.Newcombo.push(data.web3.utils.hexToNumber(comboArr[a].returnValues[0].hex))
        }
        data.usrPotato = Number(data.Newcombo[0] * 200) + Number(data.Newcombo[1] * 400) + Number(data.Newcombo[2] * 800)
      },
      getAllFarmType: async() => {
        data.allFarm = await data.OpenContract.methods.getAllFarmType().call();
        let allFarmCalls = [];
        for(var i = 1; i <= Number(data.allFarm.length); i++){
          allFarmCalls.push({ reference: 'allFarmNum', methodName: 'balanceOf', methodParameters: [data.address, i]});
        }
        const NumCallContext = [
          {
            reference: 'OpenContract',
            contractAddress: config.OpenAddress,
            abi: openAbi,
            calls: allFarmCalls
          }
        ]
        const multicallNum = new Multicall({ multicallCustomContractAddress: config.multicallAddress, web3Instance: data.web3, tryAggregate: false });
        const resultsNumber = await multicallNum.call(NumCallContext);
        let NumberArr = resultsNumber.results.OpenContract.callsReturnContext
        data.NewNUmber = []
        for(var a = 0; a < NumberArr.length; a++) {
          data.NewNUmber.push(data.web3.utils.hexToNumberString(NumberArr[a].returnValues[0].hex))
        }
      },
      handleToChart: () => {
        let domToTop = document.getElementById("mechan").offsetTop
        window.scrollTo({
        'top': domToTop,
        'behavior': 'smooth'
        })
      }
    })
    if (data.address) {
      data.initContract()
    }
    data.clipboard = new Clipboard('.clip-button')
    data.clipboard.on('success', function (e) {
      Toast(t('index.複製成功!'))
    })
    data.clipboard.on('error', function () {
      Toast(t('index.複製失敗'))
    })
    onBeforeUnmount(() => {
      if(data.clipboard){
        data.clipboard.destroy();
      }
    })
    watch(() => data.allcombo, (val) => {
      if (val) {
        data.getComboNumber()
      }
    })
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
