  text-align: center;
  font-size: 20px;
  font-weight: bold;
  color: #FFFFFF;
  padding: 0 24px;
  line-height: 27px;
  background: linear-gradient(90deg, #F7FF0F 10.64453125%, #08D51B 45.7275390625%, #0FEBFF 95.361328125%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
