  line-height: 29px;
  word-break:break-all;
  .block-info__color{
    color: #FEF846;
  }
  .block-img{
    margin: 29px 0 19px;
    img{
      width: 100%;
    }
  }
  .black-btn__mask{
    height: 39px;
    line-height: 39px;
    background: rgba(0, 0, 0, .3);
    border-radius: 5px;
    font-size: 21px;
    color: #FFFFFF;
    text-align: center;
    margin-top: 15px;
  }
}
.block-info{
  position: relative;
