      usrPotato: 0,
      Newcombo: [],
      info: '<p class="MsoNormal" style="text-indent:90.0500pt;"><b><span style="font-family:等線;font-size:18pt;"><div ksdocclipboard="ksDocClipboardId:"{7554987e-8350-9f58-8040-fadfc5251833-47426917156}";from:"wps";priorityFormat:"Kingsoft WPS 9.0 Format";mimetypes:"Kingsoft WPS 9.0 Format;text/html""style="font-size:medium;white-space:normal;"><p class="MsoNormal" align="center" style="margin:0pt 0pt 0.0001pt;text-align:center;font-family:等線;font-size:10.5pt;"><b><span style="font-size:14px;">Game pre-sale notice</span></b><b><span style="font-size:36pt;"><o:p></o:p></span></b></p><p class="MsoNormal" align="center" style="margin:0pt 0pt 0.0001pt;text-align:center;font-family:等線;font-size:10.5pt;text-indent:36pt;"><b><span style="font-size:18pt;">&nbsp;</span></b></p><p class="MsoNormal" align="center" style="margin:0pt 0pt 0.0001pt;text-align:center;font-family:等線;font-size:10.5pt;"><b><span style="font-size:12px;">Scarce Lucky Treasure Box</span></b><b><span style="font-size:22pt;"><o:p></o:p></span></b></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><b><span style="font-size:9px;">一、</span></b><b><span style="font-size:9px;">Assets and skills included:</span></b><b><span style="font-size:14pt;"><o:p></o:p></span></b></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;text-indent:28pt;"><span style="font-size:9px;">The lucky treasure box will contain up to eight types of NFT assets, namely:</span><span style="font-size:14pt;"><o:p></o:p></span></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><span style="font-size:9px;">1. Game offensive and defensive roles: fox, stunt: help the owner steal potatoes and grab wealth; dog, stunt: catch the fox for the owner and defend the potatoes; bear, stunt: help the owner attack the dog and win the bounty; gun, stunt: substitute The owner hunts bears and guards the manor!</span><span style="font-size:14pt;"><o:p></o:p></span></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;text-indent:28pt;"><span style="font-size:9px;">Rare Conferred God Characters: Nine-Tailed Fox, Snarling Dog, Tai Chi Bear, Golden Spear, Magic Skills: Immortal real body, disregarding the heroes; arrogant grabbing, lawlessness!</span><span style="font-size:14pt;"><o:p></o:p></span></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><span style="font-size:9px;">2. Rare Conferred God Characters: Conferred God rare varieties are a BUG-level surprise design in the game. Except for the corresponding Conferred God character that can be suppressed, they can plunder all over the place, accumulate wealth, and do whatever they want. Even if the magic is caught by Xiangke』s Fengshen role, it does not participate in the destruction mechanism, but the ownership and control rights belong to the new owner, which supports the resale of the NFT exchange on the chain.</span><span style="font-size:14pt;"><o:p></o:p></span></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><b><span style="font-size:9px;">二、</span></b><b><span style="font-size:9px;">Asset types and technical attributes</span></b><b><span style="font-size:14pt;"><o:p></o:p></span></b></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;text-indent:28pt;"><span style="font-size:9px;">The flat price of each lucky treasure box is 299 US dollars. The internal assets will cover (fox, dog, bear, gun) four game characters and (nine-tailed fox, snarling dog, Tai Chi bear, golden gun) four rare species of conferred gods, a total of eight game character assets. Each character acquired by the player has a unique code, which is based on the NFT digital collections issued by the BSC public chain BEP1155 agreement and BEP721 agreement. , Other characters capture both destruction and deflation mechanisms. In addition to supporting in-game NFT exchange transactions, it also supports any on-chain NFT exchange.</span><span style="font-size:14pt;"><o:p></o:p></span></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><b><span style="font-size:9px;">三、</span></b><b><span style="font-size:9px;">Pre-sale mechanism and distribution rules</span></b><b><span style="font-size:14pt;"><o:p></o:p></span></b></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;text-indent:28pt;"><span style="font-size:9px;">The pre-sale price of each lucky treasure box is the same, using a blind box mechanism, and is sold in limited quantities; there is no limit to purchase, while stocks last. Each treasure box guarantees to get 16 NFT character tokens, and the price of all the game characters in the pre-sale period is more than the price after the game is launched. After the purchase, each player"sbidding for the character types and value of the treasure box are randomly assigned, all by luck. The limited total number of rare characters of Conferred Gods are: 9 nine-tailed foxes, 27 snarling dogs, 3 Tai Chi bears, 9 golden guns, which are only randomly fired in the lucky treasure box during the pre-sale period, and the open contract address is accepted. Supervised, after the game is officially launched, the platform will no longer sell the NFT game assets of the rare characters of Fengshen.</span><span style="font-size:14pt;"><o:p></o:p></span></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><span style="font-size:14pt;">&nbsp;</span><span style="font-size:14pt;">&nbsp;&nbsp;&nbsp;&nbsp;</span><span style="font-size:14pt;"><o:p></o:p></span></p><p class="MsoNormal" align="center" style="margin:0pt 0pt 0.0001pt;text-align:center;font-family:等線;font-size:10.5pt;"><b><span style="font-size:12px;">Limited value treasure chest</span></b><b><span style="font-size:22pt;"><o:p></o:p></span></b></p><p class="MsoNormal" align="center" style="margin:0pt 0pt 0.0001pt;text-align:center;font-family:等線;font-size:10.5pt;"><b><span style="font-size:22pt;">&nbsp;</span></b></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><b><span style="font-size:9px;">一、</span></b><b><span style="font-size:9px;">&nbsp;Introduction to land asset attributes and output value</span></b><b><span style="font-size:14pt;"><o:p></o:p></span></b></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><span style="font-size:9px;">1. Yellow land, land value: Obtain game qualifications, own a potato farm, and enjoy the rights to control game characters. The daily potato (PTC) output is as much as 1% of the number of potato coins purchased on the land.</span><span style="font-size:14pt;"><o:p></o:p></span></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><span style="font-size:9px;">2. Red land, land value: enjoy all the rights and interests of the yellow land and all the rights and interests of the potato farm, and the daily potato (PTC) output is 3 times that of the yellow land.</span><span style="font-size:14pt;"><o:p></o:p></span></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><span style="font-size:9px;">3. Black land, land value: enjoy all the rights and interests of the red land and all rights and interests of the potato manor game, and the daily potato (PTC) output is 3 times that of the red land.</span><span style="font-size:14pt;"><o:p></o:p></span></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><b><span style="font-size:9px;">二、</span></b><b><span style="font-size:9px;">Introduction to the price and configuration of the premium treasure box:</span></b><b><span style="font-size:14pt;"><o:p></o:p></span></b></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;text-indent:28pt;"><span style="font-size:9px;">Treasure chests can be divided into three types according to land attributes:</span><span style="font-size:14pt;"><o:p></o:p></span></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><span style="font-size:9px;">1. Treasure Box A ($591): 9 pieces of yellow land, 9 dogs, 1 gun, and 200 potato coins.</span><span style="font-size:14pt;"><o:p></o:p></span></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><span style="font-size:9px;">2. Treasure Box B ($1137): 9 pieces of red land, 9 dogs, 1 gun, and 400 potato coins.</span><span style="font-size:14pt;"><o:p></o:p></span></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><span style="font-size:9px;">3. Treasure Box C (US$2769): 9 pieces of black land, 9 dogs, 1 gun, 800 potato coins.</span><span style="font-size:14pt;"><o:p></o:p></span></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><b><span style="font-size:9px;">三、</span></b><b><span style="font-size:9px;">&nbsp;Land types and technical attributes</span></b><b><span style="font-size:14pt;"><o:p></o:p></span></b></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;text-indent:28pt;"><span style="font-size:9px;">The limited premium treasure box adopts a transparent snap-up mechanism that clearly informs the number and types of game assets in the box. There are three types of treasure chests, A, B, and C. The land attributes contained in each type of treasure chest are different from the number of preferentially allocated Potato Coins (PTC), resulting in different values </span><span style="font-family:&quot;font-size:14pt;"></span><span style="font-size:9px;">and prices of the three treasure chests. Each piece of yellow land, red land, and black land is based on the BSC public chain BEP1155 protocol. According to the demand of the Tudou Manor game, the NFT digital assets that are constantly developed are unique and scarce, and support on-chain transactions. The main function of the early land is the mining function of potatoes (PTC) produced by the player"s planting. After the mining period is over, the game will open the land meta-universe sandbox function to holders. The landlord can build the land and control its use independently, and can set prices on all NFT exchanges on the chain according to the location of the land and the construction of facilities. sell.</span><span style="font-size:14pt;"><o:p></o:p></span></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><b><span style="font-size:9px;">四、</span></b><b><span style="font-size:9px;">Potato coin usage and technical attributes</span></b><b><span style="font-size:14pt;"><o:p></o:p></span></b></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;text-indent:28pt;"><span style="font-size:9px;">In addition to the land, the limited value treasure chest is also equipped with defensive game character dogs and guns, so as to protect the land gains obtained by the novice players who purchase the land from being attacked and plundered by other old players. The treasure chest is also equipped with a small amount of common currency potato coins that can be purchased in the game shop. Tudou Coin is the only digital asset for players to consume and entertain in all ecological scenes in the game. Tudou Coin (PTC) is a game token developed with a constant amount based on the BEP20 protocol of the BSC public chain. Potato coins used by all players participating in the game to purchase land will be publicly destroyed by the on-chain contract. The daily destruction amount will even be higher than the daily output of the extreme deflation mechanism, so that one coin is hard to find and the price of the coin continues. Climbing economic model. Tudou Coin supports DEX and mainstream exchange transactions, and is the main medium for the exchange and transaction of Tudou Manor game assets and players" real wealth. The pre-sale period is to prevent players from privately building trading pairs in DEX before the game is launched. PTC tokens will be delayed until the game is launched and sent to the wallet addresses of players participating in the bidding.Limited value treasure chest</span><span style="font-size:14pt;"><o:p></o:p></span></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><b><span style="font-size:9px;">五、</span></b><b><span style="font-size:9px;">&nbsp;Timeline of the game"s public beta progress</span></b><b><span style="font-size:14pt;"><o:p></o:p></span></b></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><span style="font-size:9px;">Early December 2021, pre-sale of global lucky treasure boxes and value packages</span><span style="font-size:9px;">.</span><span style="font-size:14pt;"><o:p></o:p></span></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><span style="font-size:9px;">In early December 2021, all props and equipment will be pre-sold out of the box.</span><span style="font-size:14pt;"><o:p></o:p></span></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><span style="font-size:9px;">In mid-December 2021, the in-game beta version will be released.</span><span style="font-size:14pt;"><o:p></o:p></span></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><span style="font-size:9px;">Mid-December 2021, Miami Community Meeting, USA.</span><span style="font-size:14pt"><o:p></o:p></span></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><span style="font-size:9px;">Late December 2021, European Berlin Technical Seminar.</span><span style="font-size:14pt;"><o:p></o:p></span></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><span style="font-size:9px;">In early&nbsp;January 2022, Tokyo, Japan Global Launch Conference.</span><span style="font-size:14pt;"><o:p></o:p></span></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><span style="font-size:9px;">In early -January 2022, the global public beta is online.</span><span style="font-size:14pt;"><o:p></o:p></span></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><span style="font-size:9px;">&nbsp;</span></p><p class="MsoNormal" style="margin:0pt 0pt 0.0001pt;text-align:justify;font-family:等線;font-size:10.5pt;"><b><span style="font-size:9px;">This notice explains that the final interpretation right belongs to Potatomanor Fund</span></b><b><span style="font-size:14pt;"><o:p></o:p></span></b></p></div></span></b></p><p class="MsoNormal"><span style="font-family:等線;font-size:14.0000pt;"></span> </p>',
      actions : [
        { text: '中文' },
        { text: 'English' }
      ],
      onSelect: (val) => {
        let lang = val.text === 'English' ? 'en' : 'zh-tw'
        setLang(lang)
        data.local = lang
        location.reload()
      },
      initContract: () => {
        data.OpenContract = new data.web3.eth.Contract(openAbi, config.OpenAddress)
        data.UsdtContract = new data.web3.eth.Contract(tokenAbi, config.UsdtAddress)
        data.initData()
      },
      initData: () => {
        data.shareUrl = `${window.location.origin}${window.location.pathname}#/?intro=${data.address}`
        data.getopenUsdt()
        data.getTokenAllowance()
        data.getAllFarmType()
      },
      getTokenAllowance: async () => {
        let wResult = await data.UsdtContract.methods.allowance(data.address, config.OpenAddress).call();
        data.NewAllowance = wResult;
      },
      approve: async () => {
        if (!data.address) {
          Toast(t('game.連線錢包'))
          return
        }
        data.allowance = await data.UsdtContract.methods.approve(config.OpenAddress, MaxUint256).send({from: data.address}).then(() => {
          Toast(t('game.授權成功'))
        })
        data.getTokenAllowance()
      },
      getopenUsdt: async() => {
        data.UsdtBlance = await data.UsdtContract.methods.balanceOf(data.address).call()
        data.decimals = await data.UsdtContract.methods.decimals().call();
      },
      buyCombo: async(price, id) => {
        if (!data.address) {
          Toast(t('game.連線錢包'))
          return
        }
        if(Number(price) > Number(data.UsdtBlance)) {
          Toast('Usdt balance is insufficient')
          return
        }
        let gas = await data.OpenContract.methods.buyCombo(id, data.intro).estimateGas({from: data.address})
        await data.OpenContract.methods.buyCombo(id, data.intro).send({from: data.address, gas: gas}).then((res) => {
          Toast('Successful purchase')
          emit('getAllCombo')
        })
      },
      getComboNumber: async() => {
        let allcomboType = []
        for(var i = 0; i < data.allcombo.length; i++) {
          allcomboType.push({ reference: 'allcomboType', methodName: 'userComboAmount', methodParameters: [data.address, data.allcombo[i].id]});
        }
        const comboCallContext = [
          {
            reference: 'OpenContract',
            contractAddress: config.OpenAddress,
            abi: openAbi,
            calls: allcomboType
          }
        ]
        const multicallCom = new Multicall({ multicallCustomContractAddress: config.multicallAddress, web3Instance: data.web3, tryAggregate: false });
        const resultsCombo = await multicallCom.call(comboCallContext);
        let comboArr = resultsCombo.results.OpenContract.callsReturnContext
        data.Newcombo = []
        for(var a = 0; a < comboArr.length; a++) {
          data.Newcombo.push(data.web3.utils.hexToNumber(comboArr[a].returnValues[0].hex))
        }
        data.usrPotato = Number(data.Newcombo[0] * 200) + Number(data.Newcombo[1] * 400) + Number(data.Newcombo[2] * 800)
      },
      getAllFarmType: async() => {
        data.allFarm = await data.OpenContract.methods.getAllFarmType().call();
        let allFarmCalls = [];
        for(var i = 1; i <= Number(data.allFarm.length); i++){
          allFarmCalls.push({ reference: 'allFarmNum', methodName: 'balanceOf', methodParameters: [data.address, i]});
        }
        const NumCallContext = [
          {
            reference: 'OpenContract',
            contractAddress: config.OpenAddress,
            abi: openAbi,
            calls: allFarmCalls
          }
        ]
        const multicallNum = new Multicall({ multicallCustomContractAddress: config.multicallAddress, web3Instance: data.web3, tryAggregate: false });
        const resultsNumber = await multicallNum.call(NumCallContext);
        let NumberArr = resultsNumber.results.OpenContract.callsReturnContext
        data.NewNUmber = []
        for(var a = 0; a < NumberArr.length; a++) {
          data.NewNUmber.push(data.web3.utils.hexToNumberString(NumberArr[a].returnValues[0].hex))
        }
      },
      handleToChart: () => {
        let domToTop = document.getElementById("mechan").offsetTop
        window.scrollTo({
        'top': domToTop,
        'behavior': 'smooth'
        })
      }
    })
    if (data.address) {
      data.initContract()
    }
    data.clipboard = new Clipboard('.clip-button')
    data.clipboard.on('success', function (e) {
      Toast(t('index.複製成功!'))
    })
    data.clipboard.on('error', function () {
      Toast(t('index.複製失敗'))
    })
    onBeforeUnmount(() => {
      if(data.clipboard){
        data.clipboard.destroy();
      }
    })
    watch(() => data.allcombo, (val) => {
      if (val) {
        data.getComboNumber()
      }
    })
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
